*{
    box-sizing: border-box;
    padding: 0cm;
    margin: 0cm;
}


.MainDiv{
    width: 100%;
    height: 100vh;
    background-color:gray;
    display: flex;
    flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
  
  }
  .CenterDiv{
    width: 25%;
    height: 70vh;
    background-color:blueviolet;
    box-shadow: 5px 5px 25px - 5px rgba(0,0,0,0.5);
    border-radius: 15px;
  
  
  }
  h1{
      color:rgba(0, 0, 0, 0.87);
      width: 25%;
      font-size: 20px;
      margin-bottom: 200px;
      margin-top: 20px;
      margin-left: 40px;
      margin-right: 20px;
      min-width: 64px;
      box-sizing: border-box;
      font-weight: 500;
      border-radius: 4px;
      letter-spacing: 0.02857em;
      text-transform: uppercase;
      text-shadow: rgba(0, 0, 0, 0.87);
      text-align: center;
  background: springgreen;
      


  }
  input{
      text-align: center;
      height: 30px;
      border: none;
      font-size: 20px;
      font-weight: 500;
      width: 60%;
      margin-top: 60px;
      border-bottom: 20px;
      margin-right: 150px;
  }
  .newBtn{
      width: 50px;
      height: 55px;
      background-color: salmon;
      border-radius: 50% !important;
      margin-top: 100px;
      border-bottom: 20px;
      margin-left: 80px;
      margin-right: 80px;
  }
  ol{
      margin-top: 30px;
      text-align: right;

  }
  ol li{
      padding-left: 0px;
      text-align: right;
      font-size: 20px;
      font-weight: 500;
      min-height: 40px;
      display: flex;
      align-items: center;
      text-transform: uppercase;
      list-style: none;
  }

      .todo_style{

        display: flex;
        flex-direction:row;
        justify-content:left;
         align-items:center;
      

      
      
  }
  .CenterDiv{
      width: 55%;
  }
 