body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.Header{
  color:skyblue;
  text-align: center;
  background-color: blueviolet;
  transition: width 10s;
  text-shadow: 1px 1px;
}
.Heading2{
  color:violet;
  text-align: center;
  background-color:aqua;
}
.list{
  color:violet;
  text-align: center;

}
.DT{
  color:violet;
  text-align: center;

}
.images{
  color:blueviolet;
  text-align: center;
}

.Card:hover{
box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.1);
  transform: scale(1.03,1.03);
}
.IMG{
  width: 255px;
height: 255px;
border-top-left-radius: 12px;
border-top-right-radius: 12px;
padding: 16px 24px 24px 24px;
}
.container {
  margin-top: 100px;
  margin-bottom: 100px;
  margin-right: 50px;
  margin-left: 50px;
}
.card2 {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  width: 50%;
  float: left;
  padding: 20px;
  border: 2px solid red;
}
.Card{
  border: 3px solid #fff;
  padding: 20px;
}
.ButtonClick{
  min-height: 40px;
  width: 90px;
  padding: 5px 5px;
  background:violet;
  border: 5px solid black;
  color:black;
  border-radius: 1px;
  text-transform: lowercase 50px;
  margin-right: 50px;
  margin-left: 0px;
  
}

.CenterDiv{
  width: 25%;
  height: 80vh;
  display: flex;
  background-color:white;
  box-shadow: 5px 5px 25px - 5px rgba(0,0,0,0.5);
  border-radius: 15px;


}
